<template>
  <div class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="beforeClose">返回</el-button>
      </div>
      <div class="fr">
        <el-button type="primary" @click="handlePrintTemplate">打印预览</el-button>
      </div>
    </el-row>
    <el-collapse accordion>
      <el-collapse-item title="单据信息" name="1">
        <el-descriptions :column="2">
          <el-descriptions-item label="凭证日期">{{ form.documentDate || '---' }}</el-descriptions-item>
          <el-descriptions-item label="记账日期">{{ form.accountingDate || '---' }}</el-descriptions-item>
          <el-descriptions-item label="收货工厂">{{ form.receiptFactoryName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="单据编号">{{ form.billNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="收货仓库">{{ form.receiptWarehouse || '---' }}</el-descriptions-item>
          <el-descriptions-item label="移动类型">{{ form.movementType || '---' }}</el-descriptions-item>
          <el-descriptions-item label="成本中心">{{ form.costCenterName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="总账科目">{{ form.ledgerSubjects || '---' }}</el-descriptions-item>
          <el-descriptions-item label="供应商">{{ form.supplierName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="采购员/电话号">{{ form.buyerName || '---' }} {{ form.buyerTel || '---' }}</el-descriptions-item>
          <el-descriptions-item label="资产">{{ form.asset || '---' }}</el-descriptions-item>
          <el-descriptions-item label="采购订单号">{{ form.purchaseOrderNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="网络">{{ form.network || '---' }}</el-descriptions-item>
          <el-descriptions-item label="订单">{{ form.orderNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="项目">{{ form.project || '---' }}</el-descriptions-item>
          <el-descriptions-item label="合同号/汇总号">{{ form.contractCode || '---' }}</el-descriptions-item>
        </el-descriptions>
      </el-collapse-item>
    </el-collapse>
    <!-- 单据类型选择 -->
    <div style="margin: 20px 0;">
      <el-descriptions :column="4">
        <el-descriptions-item label="单据类型">{{ pullListMap[form.type] }}</el-descriptions-item>
        <el-descriptions-item label="说明">{{ form.explanation }}</el-descriptions-item>
        <el-descriptions-item label="入库项">{{ form.stockInNumber }}</el-descriptions-item>
        <el-descriptions-item label="数量">{{ form.deliveredNumber }}</el-descriptions-item>
      </el-descriptions>
    </div>

    <!-- 头部搜索 -->
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" :disabled="!form.id" @click="handleModify(scope.row)">详细</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!-- 详情 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :stock-id="form.id"
      @confirm="init"
    />

    <print-templates v-model="dlgVisible" title="打印模板" :extra-info="curRow" />

  </div>
</template>

<script>
import tags from '@/mixins/tags'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import PrintTemplates from '@/views/in-storage/info/warehousingEntryOrder/PrintTemplates'
import { getDetail2Api as getInfoApi, getList2Api, submitDetailApi } from '@/apis/modules/in-storage/info/receipt-detail'
import { typeMap } from '@/views/in-storage/info/status'
import { infoPrintMixins } from '@/views/in-storage/info/print.mixins'

export default {
  name: 'InStorageInfoReceipt',
  components: {
    EditDialog,
    PrintTemplates
  },
  mixins: [tags, paginationPage, infoPrintMixins],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {},
      form: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编码',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '规格型号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        // 入库管理/入库单信息 实收数量改为入库数量
        {
          module: 'word',
          prop: 'stockInNumber',
          // label: '实收数量',
          label: '入库数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
        // 入库管理/入库单信息 去掉未入库数量展示项
        // {
        //   module: 'word',
        //   prop: 'unreceivedNumber',
        //   label: '未入库数量',
        //   align: 'center',
        //   minWidth: '',
        //   fixed: false,
        //   sortable: false,
        //   resizable: true,
        //   formatter: null,
        //   headerAlign: 'center'
        // }
      ],
      columnSets: [],
      dialogForm: {},
      pullListMap: typeMap,
      pullList: [
        {
          code: '1',
          name: '常规'
        },
        {
          code: '2',
          name: '战略'
        }
      ],
      editType: 'add',
      dialogVisible: false,
      params: {},
      dlgVisible: false, // 打印模板弹窗
      curRow: {} // 当前点击的入库单信息
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.params = this.$route.params
    this.init()
    this.initInfo()
  },
  methods: {
    init(type) {
      console.log('init method')
      this.loading = true
      const params = {
        stockInId: this.params.id,
        filterStockInNumber: true,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getList2Api({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initInfo() {
      this.loading = true
      const params = {
        entity: { id: this.params.id }
      }
      return new Promise((resolve, reject) => {
        getInfoApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.form = { ...data }
            if (this.form.billNumber) {
              // 修改当前tags
              this.setTagsTitleMixins(null, this.form.billNumber)
            }
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    confirmDialog() {
      this.loading = true
      const params = {
        entity: {
          ...this.form
        },
        wsmList: this.params.ids.map(i => {
          return {
            deliveryMaterialId: i
          }
        })
      }
      submitDetailApi(params).then(res => {
        const { code, data } = res
        if (!code) {
          // this.initInfo()
          this.$set(this.form, 'id', data)
        }
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    beforeClose() {
      this.$router.go(-1)
      this.$store.dispatch('tagsView/delView', this.$route)
    },
    // 打印预览
    handlePrintTemplate() {
      const { type, explanation } = this.form
      this.handlePrint(this.form.id, '', { type, explanation })
      // this.form.type === 2 && this.handlePrint(this.form.id)
      // this.form.type === 1 && (this.dlgVisible = true, this.curRow = this.form)
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
