// 按推荐库位将物料入库(参数：stockInId  入库单id deliveryMaterialId  交接物料ID storageId  推荐库位id)
export function recommendWarehousingApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsStockIn/addPutInStorageByRecommendStorageId',
    method: 'post',
    data
  })
}
